/** @jsx jsx */
import { jsx, Text } from 'theme-ui'
import React from 'react'

import PropTypes from 'prop-types'

const GroupSubhead = ({ groupSubhead, ...props }) => (
	<Text
		// {...animation.slideUp1}
		{...props}
		sx={{
			fontFamily: 'heading',
			paddingLeft: 0,
			textTransform: 'uppercase',
			letterSpacing: 'lazy',
			fontWeight: ['semibold', 'semibold', 'semibold', 'semibold', 'semibold'],
			fontSize: [0, 2, 1, 1, 1],
			gridColumn: '1/3',
			gridRow: '1/1',
			color: 'text',
			// borderBottom: (theme) => `thin solid ${theme.colors.primaryLight}`,
			mb: [3, 3, 2, 2, 2],
			pb: [3, 3, 0, 0, 0],
			// ml: [-2, -2, 0, 0, 0],
			// pl: [2, 2, 2, 2, 2],
			// borderRadius: 2,
		}}
	>
		{groupSubhead}
	</Text>
)

GroupSubhead.propTypes = {
	groupSubhead: PropTypes.any,
}

GroupSubhead.defaultProps = {
	groupSubhead: 'Group Subheads',
}

export default GroupSubhead
