/** @jsx jsx */
import { jsx, Box } from 'theme-ui'
import React from 'react'

import PropTypes from 'prop-types'
import GroupGrid from '../GroupGrid/GroupGrid.jsx'
import GroupHead from '../GroupHead/GroupHead.jsx'
import ProgressBarGroup from '../ProgressBarGroup/ProgressBarGroup.jsx'
import LabeledProgressBar from '../../../../system/molecules/staticElements/LabeledProgressBar/LabeledProgressBar.jsx'
import Groups from '../progressBarData.js'

const ProgressBarGroupMap = ({
	groupGridColumns,
	groupSubGridColumns,
	groupHead,
	groupsSubhead,
	progressLabel,
	done,
	...props
}) => {
	return (
		<Box
			className='gridWrapper'
			{...props}
			sx={{
				// bg: 'white',
				width: '100%',
				borderRadius: 6,
				py: 6,
				// px: [1, 1, 4, 6, 6],
				pr: [0, 0, 0, 5, 4],
				mt: 4,
				display: ['none', 'none', 'block', 'block', 'block'],
			}}
		>
			<Box className='grid'>
				{Groups.map((group, id) => (
					<>
						<GroupHead key={id} groupHead={group.groupHead} />
						<GroupGrid groupGridColumns={group.groupGridColumns}>
							{group.skillSets.map((skillSet, id) => (
								<ProgressBarGroup
									key={id}
									groupSubhead={skillSet.groupSubhead}
									groupSubGridColumns={skillSet.groupSubGridColumns}
								>
									{skillSet.bars.map((bar, id) => (
										<LabeledProgressBar
											key={id}
											progressLabel={bar.label}
											done={bar.done}
										/>
									))}
								</ProgressBarGroup>
							))}
						</GroupGrid>
					</>
				))}
			</Box>
		</Box>
	)
}

ProgressBarGroupMap.propTypes = {}

ProgressBarGroupMap.defaultProps = {}

export default ProgressBarGroupMap
