/** @jsx jsx */
import PropTypes from 'prop-types'
import { jsx, Text, Box, Link } from 'theme-ui'
import React from 'react'

import { ImArrowUpRight } from 'react-icons/im'

// import Icon from '../../../atoms/icons/Icon'
// import AnchorLink from '../../../atoms/AnchorLink'

const CtaLink = ({ linkTo, linkText, linkVisual, color, ...props }) => (
	<>
		<Link
			href={linkTo}
			className={props.className}
			{...props}
			sx={{
				display: 'inline-block',
				verticalAlign: 'middle',
			}}
			// sx={{
			// 	display: 'inline-block',
			// }}
			// sx={{
			// width,
			// height,
			// bg,
			// padding,
			// margin,
			// color,
			// display: 'inline-flex',

			// }}
		>
			<Text
				{...props}
				className='linkText'
				sx={{
					color,
					display: 'inline-block',
				}}
			>
				{linkText}
			</Text>
			<Box
				{...props}
				className='linkVisual'
				sx={{
					display: 'inline-block',
				}}
			>
				{linkVisual}
			</Box>
		</Link>
	</>
)

CtaLink.propTypes = {
	className: PropTypes.any,
	iconColor: PropTypes.any,
	iconMarginLeft: PropTypes.any,
	linkVisual: PropTypes.any,

	linkText: PropTypes.any,
	linkTo: PropTypes.any,
}

CtaLink.defaultProps = {
	// height: '',
	// margin: '',
	// padding: '',
	// width: '',
	// bg: 'transparent',
	// color: 'text',
	// linkTo: '/',
	// linkText: 'Link',
	linkVisual: <ImArrowUpRight />,
	// iconSize: '.5em',
	// iconColor: 'primary',
	// iconMarginLeft: 1,
	className: 'ctaLink',
	linkText: 'Link',
	linkTo: '/',
}

export default CtaLink
